import cn from 'classnames';

export const Tabs = (props) => {
  const { tabs, selectedTabId, selectTab } = props;

  // const isTabCurrent = (id) => {

  // }

  return (

    <div className="tabs is-boxed">
      <ul>
        {tabs.map(tab => (
          <li
            className={cn({ 'is-active': selectedTabId === tab.id })}
            key={tab.id}
            data-cy="Tab"
          >
            <a
              href={`#${tab.id}`}
              data-cy="TabLink"
              onClick={() => selectTab(tab.id)}
            >
              {tab.title}
            </a>
          </li>
        ))}
        {/* <li className="is-active" data-cy="Tab">
            <a href="#tab-1" data-cy="TabLink">Tab 1</a>
          </li>

          <li data-cy="Tab">
            <a href="#tab-2" data-cy="TabLink">Tab 2</a>
          </li>

          <li data-cy="Tab">
            <a href="#tab-3" data-cy="TabLink">Tab 3</a>
          </li> */}
      </ul>
    </div>
  );
};
